@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap);
.App {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  background-color: #4158D0;
  background-image: linear-gradient(90deg, #4158D0 0%, #C850C0 100%);
}

body {
  padding: 0;
  margin: 0;
}
:root {
    --navbarBackground: rgb(148,0,211);
    --text: rgb(0,0,0);
}

* {
    font-family: "Roboto", sans-serif;
  }

.home {
    width: 100%;
    align-items: center;
    background-color: #4158D0;
    background-image: linear-gradient(90deg, #4158D0 0%, #C850C0 100%);
  }
 
.gradient {
    background: linear-gradient(90deg, rgba(57,0,86,1) 0%, rgba(78,0,71,1) 100%);    
    -webkit-background-clip: text;    
            background-clip: text;
    -webkit-text-fill-color: transparent;
}

.about {
    width: 100%;
    height: calc(100vh - 100px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #4158D0;
    background-image: linear-gradient(90deg, #4158D0 0%, #C850C0 100%);
    color: #f0f0f0;
  }
  
.about h2 {
    font-size: 3rem;
    color: --text;
    height: 2rem;
  }
  
.about .prompt {
    width: 90%;
    font-size: 2rem;
  }

.prompt svg {
    font-size: 60px;
    margin: 5px;
    color: white;
  }

.prompt svg:hover {
    font-size: 60px;
    margin: 5px;
    color: rgba(57,0,86,1);
  }
  
.skills {
    font-size: 35px;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  
.skills h1 {
    color: #3e497a;
  }
  
.skills .list {
    list-style: none;
    width: 100%;
    padding: 0%;
  }
  
.list span {
    font-size: 20px;
  }
  
@media only screen and (max-width: 600px) {
.about h2 {
    font-size: 40px;
}
  
.about .prompt {
    margin-top: 1.2rem;
    font-size: 20px;
    width: 80%;
}

.skills {
    text-align: center;
}
.list {
    padding: 0;
}

.skills h2 {
    font-size: 30px;
}
}
:root {
  --accent1: #971d15;
  --accent2: #f9433dcc;
  --navbarBackground: -webkit-linear-gradient(var(--accent2), var(--accent1));
}

* {
    font-family: "Roboto", sans-serif;
  }

.navbar {
    width: 100%;
    height: 100px;
    background-color: #4158D0;
    background-image: linear-gradient(90deg, #4158D0 0%, #C850C0 100%);
  }
  
  .links {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
  }
  
  .navbar a {
    color: white;
    text-decoration: none;
    margin: 2rem;
    font-size: 1.6rem;
  }
  
  .toggleButton {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  
  .toggleButton svg {
    font-size: 50px;
  }
  .toggleButton button {
    margin-right: 20px;
    background-color: transparent;
    border: none;
    color: white;
    cursor: pointer;
  }
  
  #open {
    height: 100vh;
  }
  
  #open .links {
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 100px;
  }
  
  #open a {
    width: 100%;
    text-align: center;
  }
  
  @media only screen and (max-width: 900px) {
    .navbar a {
      width: 80vw;
    }
  }
  
  @media only screen and (max-width: 600px) {
    .toggleButton {
      display: flex;
    }
    #close a {
      display: none;
    }
  }
  
  @media only screen and (min-width: 601px) {
    .toggleButton {
      display: none;
    }
  
    .hiddenLinks {
      display: none;
    }
  
    #open {
      height: 100px;
    }
  
    #open .links {
      flex-direction: row;
    }
  }
:root {
    --footerBackground: rgb(148,0,211)
}

* {
    font-family: "Roboto", sans-serif;
  }

.footer {
    width: 100%;
    height: 200px;
    background-color: #4158D0;
    background-image: linear-gradient(90deg, #4158D0 0%, #C850C0 100%);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 10px;
    margin-top: 0px;
  }
  
  .socialMedia svg {
    color: white;
    margin: 1rem;
    font-size: 2rem;
    cursor: pointer;
  }
  
  .footer p {
    color: white;
  }
  
  @media only screen and (max-width: 600px) {
    .footer svg {
      font-size: 2rem;
    }
  }
